import BetterScroll from "better-scroll";
import userAPI from "./../apis/users.js";
import { fromNowFilter } from "./../utils/mixins";
import { Toast } from "./../utils/helpers";
import { mapState } from "vuex";
import tweetAPI from "./../apis/tweets.js";
export default {
  name: "UserTweetList",
  mixins: [fromNowFilter],
  data() {
    return {
      tweets: {},
    };
  },
  computed: {
    ...mapState(["userProfile"]),
  },
  watch: {
    $route() {
      this.fetchUserTweets();
    },
  },
  mounted() {
    this.fetchUserTweets();
    this.movefunction(1000);
  },
  methods: {
    async fetchUserTweets() {
      try {
        const pramsId = this.$route.params.id;
        const { data } = await userAPI.getUserTweet(pramsId);

        if (data.status === "error") {
          throw new Error(data.message);
        }

        this.tweets = data;
      } catch (error) {
        Toast.fire({
          icon: "warning",
          title: error.message,
        });
      }
    },
    movefunction(s) {
      return new Promise(function (resolve) {
        setTimeout(() => {
          resolve(
            new BetterScroll(".tweets_list", {
              mouseWheel: true, //開啟滑鼠滾動
              disableMouse: false, //關閉滑鼠拖動
              disableTouch: false, //關閉手指觸摸
              scrollX: true, //X軸滾動開啟
              click: true,
            })
          );
        }, s);
      });
    },
    async likeTweet(id) {
      try {
        const { data } = await tweetAPI.likeTweets(id);

        if (data.status === "error") {
          throw new Error(data.message);
        }

        Toast.fire({
          icon: "success",
          title: data.message,
        });

        this.fetchUserTweets();
      } catch (error) {
        Toast.fire({
          icon: "warning",
          title: error.message,
        });
      }
    },
    async unlikeTweet(id) {
      try {
        const { data } = await tweetAPI.unLikeTweets(id);

        if (data.status === "error") {
          throw new Error(data.message);
        }

        Toast.fire({
          icon: "success",
          title: data.message,
        });

        this.fetchUserTweets();
      } catch (error) {
        Toast.fire({
          icon: "warning",
          title: error.message,
        });
      }
    },
  },
};
